* {
  margin: 0;
  padding: 0;
}

body {
  color: #212121;
  background-color: #f4f4f3;
  padding: 10px;
  font-family: Arial, sans-serif;
}

h1, h2, p {
  margin: 0;
}

.gallery {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  display: grid;
}

.search-form {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 50px;
  padding: 20px 0;
  display: flex;
}

.search-form input {
  background-color: #fae4f7;
  border: none;
  border-radius: 8px;
  outline: none;
  margin-right: 8px;
  padding: 4px 8px;
  box-shadow: 0 0 0 2px #fae4f7;
}

.search-form input:focus-within {
  box-shadow: 0 0 0 2px rgba(0, 0, 0, .1);
}

.search-form button {
  cursor: pointer;
  background-color: #fae4f7;
  border: none;
  border-radius: 8px;
  padding: 4px 12px;
  transition: all .4s;
}

.search-form button:hover {
  background-color: #83a4d4;
}

img {
  height: 100%;
  width: 100%;
}

a:link {
  text-decoration: none;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.info {
  justify-content: space-between;
  gap: 8px;
  padding: 8px 12px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 4px;
  display: flex;
}

